import React from "react"
import { kClientKeyUI } from "src/helpers/K"
import styled from "styled-components"

const WrapUI = styled.div`
  display: inline-block;
  & > span {
    padding: 0px 2px;
    margin-bottom: 2px;
    background: red;
    display: inline-block;
    margin-right: 2px;
    border: 1px solid rgba(0, 126, 255, 0.08);
    border-radius: 3px;
    background-color: rgba(0, 126, 255, 0.08);
    color: #007eff;
    &.a-missing {
      color: #c7c7c7;
      background-color: #c7c7c722;
      border-color: #c7c7c755;
    }
  }
`
export default function MUIArrTags(props) {
  const { values, options } = props
  return (
    <WrapUI className="mui-arr_tags">
      {options &&
        options.length > 0 &&
        options.map((v, i) => {
          return (
            <span
              key={i}
              title={v.Text}
              className={`${
                v[kClientKeyUI._UI_Missing] === true ? "a-missing" : ""
              }`}
            >
              {v.Text}
            </span>
          )
        })}
      {values &&
        values.length > 0 &&
        values.map((v, i) => {
          return (
            <span key={i} title={v}>
              {v}
            </span>
          )
        })}
    </WrapUI>
  )
}
